@use '../..//base/variables' as v;
@use '../../base/mixins' as m;

.formulario {
    display: flex;
    flex-direction: column;

    &__fieldset {
        padding: 0;
        border: none;
        margin-top: 1rem;
    }

    &__legend {
        margin-bottom: 2rem;
        font-size: 2.2rem;
        font-weight: v.$gris;
    }

    &__campo {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;
    }

    &__contenedor-icono {
        display: flex;
        overflow: hidden;
        border-radius: .5rem;

    }

    &__icono {
        background-color: v.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        i {
            color: v.$blanco;
            font-size: 2rem;
        }
    }

    &__label {
        display: block;
        font-size: 1.8rem;
        color: rgba($color: v.$negro, $alpha: .8);
    }

    &__input,
    &__select{
        padding: 1rem;
        border: 1px solid darken($color: v.$gris-claro, $amount: 20%);
        border-radius: .5rem;

        &--file {
            border: none;
        }

        &--sociales{
            @extend .formulario__input;
            flex: 1;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }

    &__submit {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
        align-self: flex-start;

        &--registrar,
        &--full {
            @extend .formulario__submit;
            width: 100%;
        }
    }

    &__texto{
        margin-top: 3rem;
    }

    &__imagen{
        max-width: 20rem;
    }

    &__listado{
        @include m.tags;
    }

    &__dias{
        display: flex;
        gap: 2rem;
    }
}